
import { showDate } from "./time"
import { showGreeting } from "./greet"
import { input, getWeather } from "./weather"
import { getQuotes } from "./quote"
const button = document.querySelector('.change_language')
const input_name = document.querySelector("#username")
export let isRus = false
export function laguageEvent() {
    button.addEventListener('click', function() {
        if (button.classList.contains("en")) {
            button.classList.remove("en")
            button.textContent = "RU"
            input_name.placeholder = '[Введите имя]'
            isRus = true
            getQuotes()
            getWeather(input.value)
            showGreeting(true)
            showDate()
        } else {
            button.classList.add("en")
            button.textContent = "EN"
            isRus = false
            input_name.placeholder = '[Enter name]'
            getQuotes()
            getWeather(input.value)
            showGreeting(false)
            showDate()
        }
    })
}
