import {showGreeting} from './greet'
import { isRus } from './language'
const time = document.querySelector("#current_time h2")
const data = document.querySelector("#current_date h4")

export function showTime() {
    let date = new Date();
    let currentTime = date.toLocaleTimeString('ru-RU');
    time.textContent = currentTime.split(':').join(' : ')
    showGreeting(isRus)
    showDate(isRus)
    setTimeout(showTime, 1000);
}
export function showDate() {
    let date = new Date();
    let language = isRus ? 'ru-RU' : 'en-EN'
    let options = {month: 'long', day: 'numeric', weekday: 'long'};
    let currentDate = date.toLocaleDateString(language, options);
    data.textContent = currentDate[0].toUpperCase()+currentDate.slice(1,currentDate.length)
}
