import { isRus } from "./language";
import jsonData from '../data.json'
const quote = document.querySelector(".quote_text")
const author = document.querySelector(".quote_author")
const refresh = document.querySelector(".refresh_quote")

export function getQuotes() {

    if (!isRus) {
        let quotes = 'https://favqs.com/api/qotd';
        fetch(quotes)
            .then(res => res.json())
            .then(data => { 
                quote.textContent = data.quote.body
                author.textContent = data.quote.author
            })
            .catch(
                
            );
    } else {
        let num = (Math.floor(Math.random() * 61))
        quote.textContent = jsonData[num].text
        author.textContent = jsonData[num].author
    }

}

export function refreshEvent() {
    refresh.addEventListener("click", function() {
        getQuotes()
    })
}
