import {getTimeOfDay} from './greet'

const body = document.querySelector("body")
const slide_prev = document.querySelector(".slide_left")
const slide_next = document.querySelector(".slide_right")

let randomNum = getRandomNum()
function getRandomNum() {
    return (Math.floor(Math.random() * 20) + 1);
}

export function setBg() {
    let timeOfDay = getTimeOfDay()
    let num_to_str = randomNum+''
    let bgNum = num_to_str.padStart(2, '0');
    let img = new Image();
    img.src = `https://raw.githubusercontent.com/rolling-scopes-school/stage1-tasks/assets/images/${timeOfDay}/${bgNum}.jpg`;
    img.onload = () => {      
      body.style.backgroundImage = `url('${img.src}')`
    }; 
    
    // body.style.backgroundImage = `url('https://raw.githubusercontent.com/rolling-scopes-school/stage1-tasks/assets/images/${timeOfDay}/${bgNum}.jpg')`;
}

export function addBackgroundEvents() {
    slide_prev.addEventListener('click', function() {
        if(randomNum == 1) {
            randomNum = 20
        } else {
            randomNum--
        }
        setBg()
    });
    slide_next.addEventListener('click', function() {
        if(randomNum == 20) {
            randomNum = 1
        } else {
            randomNum++
        }
        setBg()
    });
}