import sound1 from '../sounds/1.mp3'
import sound2 from '../sounds/2.mp3'
import sound3 from '../sounds/3.mp3'
import sound4 from '../sounds/4.mp3'

let play_button = `<svg version="1.1" xmlns="http://www.w3.org/2000/svg" width="45" height="45" viewBox="0 0 64 64">
<title>play2</title>
<path d="M32 0c-17.673 0-32 14.327-32 32s14.327 32 32 32 32-14.327 32-32-14.327-32-32-32zM32 58c-14.359 0-26-11.641-26-26s11.641-26 26-26 26 11.641 26 26-11.641 26-26 26zM24 18l24 14-24 14z"></path>
</svg>`

let pause_button = `<svg version="1.1" xmlns="http://www.w3.org/2000/svg" width="45" height="45" viewBox="0 0 64 64">
<title>pause</title>
<path  d="M32 0c-17.673 0-32 14.327-32 32s14.327 32 32 32 32-14.327 32-32-14.327-32-32-32zM32 58c-14.359 0-26-11.641-26-26s11.641-26 26-26 26 11.641 26 26-11.641 26-26 26zM20 20h8v24h-8zM36 20h8v24h-8z"></path>
</svg>`
let prev = document.querySelector('.prev_audio');
let play = document.querySelector('.play_audio');
let next = document.querySelector('.next_audio');

let arr = [sound1, sound2, sound3, sound4].map(x=>{
    return new Audio(x)
})
let current_song = 0
let is_play = false
export function audioEvents() {
    play.addEventListener('click', function() {
        if (!is_play) {
            is_play = true
            arr[current_song].play()
            play.innerHTML = pause_button
            arr[current_song].onended = function() {
                is_play = false
                arr[current_song].pause()
                play.innerHTML = play_button
                document.querySelector('.list_item.active').classList.remove("active")
                if (current_song == 3) {
                    current_song = 0
                } else {
                    current_song++
                }
                document.querySelectorAll('.list_item')[current_song].classList.add('active')
            }
        } else {
            is_play = false
            arr[current_song].pause()
            play.innerHTML = play_button
        }
    })
    next.addEventListener('click', function() {
        is_play = false
        arr[current_song].pause()
        play.innerHTML = play_button
        document.querySelector('.list_item.active').classList.remove("active")
        if (current_song == 3) {
            current_song = 0
        } else {
            current_song++
        }
        document.querySelectorAll('.list_item')[current_song].classList.add('active')
    })
    prev.addEventListener('click', function() {
        is_play = false
        arr[current_song].pause()
        play.innerHTML = play_button
        document.querySelector('.list_item.active').classList.remove("active")
        if (current_song == 0) {
            current_song = 3
        } else {
            current_song--
        }
        document.querySelectorAll('.list_item')[current_song].classList.add('active')
    })

}