import { input, getWeather} from "./weather";
const greet = document.querySelector("#greeting h3")
const name = document.querySelector("#username")
export function showGreeting(rus) {

    let timeOfDay = getTimeOfDay(rus);
    let greetingText = rus ? `Добр${timeOfDay}`:`Good ${timeOfDay}`
    greet.textContent = greetingText
    
}
export function getTimeOfDay(rus) {
    let date = new Date();
    let hours = date.getHours();
    let ans = rus ? ['ое утро', "ый день", "ый вечер", "ой ночи"] : ['morning', 'afternoon', 'evening', 'night']
    if (hours >= 6 && hours < 12) {
        return ans[0]
    } else if (hours >= 12 && hours < 18) {
        return ans[1]
    } else if (hours >= 18 && hours < 24) {
        return ans[2]
    } else {
        return ans[3]
    }
}


export function setLocalStorage() {
    localStorage.setItem('name', name.value.trim());
    localStorage.setItem('city', input.value.trim())
}

export function getLocalStorage() {
  if(localStorage.getItem('name') && localStorage.getItem('name').length != 0) {   
    name.value = localStorage.getItem('name');
  }
  if(localStorage.getItem('city') && localStorage.getItem('city').length != 0) {   
    input.value = localStorage.getItem('city');
  } else {
    input.value = 'Minsk'
  }
  getWeather(input.value)
}
