import { isRus } from "./language";
const weatherIcon = document.querySelector('.weather-icon');
const temperature = document.querySelector('.temperature');
const wind_speed = document.querySelector('.wind_speed');
const humidity = document.querySelector('.humidity');
export const input = document.querySelector("#city_input")

export function getWeather(x) {  
    let lang = isRus ? 'ru' : 'en'
    const url = `https://api.openweathermap.org/data/2.5/weather?q=${x}&lang=${lang}&appid=b35aac009e4142bc8271017a321ae55f&units=metric`;

    fetch(url)
        .then(res=>res.json())
        .then(data => {
        
            if (data.cod  == 200) {
                weatherIcon.className = 'weather-icon owf';
                temperature.textContent = `${Math.floor(data.main.temp)}°C ${data.weather[0].description}`;
                wind_speed.textContent = isRus ? `Скорость ветра: ${Math.floor(data.wind.speed)} м/с` : `Wind speed: ${Math.floor(data.wind.speed)} m/s`;
                humidity.textContent = isRus ? `Влажность: ${Math.floor(data.main.humidity)} %` :`Humidity: ${Math.floor(data.main.humidity)} %`
            } else {
                weatherIcon.className = 'weather-icon owf';
                temperature.innerHTML = isRus ? `Ошибка! Данный город не найден ${x}!` :`Error! city not found for ${x}!`
                wind_speed.textContent = ''
                humidity.textContent = ''
            }

        })
        .catch(
            
        )
}

export function weatherEvents() {
    input.addEventListener('focusout', function() {
        getWeather(input.value.trim())
    })
    input.addEventListener('keydown', function(e) {
        if (e.keyCode == 13) {
            getWeather(input.value.trim())
        }

    })
}